import Vue from 'vue';
import EventBus from './EventBus';
import App from './App';

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  mounted() {
    window.addEventListener('keyup', (event) => {
      EventBus.$emit('global-key-pressed', event);
    });
  },
});



