<template>
  <div class="equation" v-on:click="handleButtonClick">
    <h1>{{ equation }}</h1>
    <div class="keypad">
      <CalculatorButton :value="7" />
      <CalculatorButton :value="8" />
      <CalculatorButton :value="9" />
      <CalculatorButton :value="'/'" :name="'divide'" />
      <br>

      <CalculatorButton :value="4" />
      <CalculatorButton :value="5" />
      <CalculatorButton :value="6" />
      <CalculatorButton :value="'*'" :name="'multiply'" />
      <br>

      <CalculatorButton :value="1" />
      <CalculatorButton :value="2" />
      <CalculatorButton :value="3" />
      <CalculatorButton :value="'-'" :name="'minus'" />
      <br>

      <CalculatorButton :value="0" />
      <CalculatorButton :value="'.'" :name="'decimal'" />
      <CalculatorButton :value="'='" :name="'equal'" />
      <CalculatorButton :value="'+'" :name="'plus'" />
      <br>

      <CalculatorButton :disabled="true" />
      <CalculatorButton :disabled="true" />
      <CalculatorButton :value="'('" :name="'open-parenthesis'" />
      <CalculatorButton :value="')'" :name="'close-parenthesis'" />
      <br>
    </div>
  </div>
</template>

<script>
import EventBus from '../EventBus';
import CalculatorButton from './CalculatorButton';
import SimpleMath, { isOperator, isDigit } from '../utils/SimpleMath';

function removeTrailingOperator(equation) {
  if (isOperator(equation.slice(-1))) {
    return equation.slice(0, -1);
  }
  return equation;
}

export default {
  name: 'Calculator',
  data() {
    return {
      equation: '',
      openGroups: 0,
    };
  },
  created() {
    EventBus.$on('global-key-pressed', this.handleKeyPressed);
  },
  destroyed() {
    EventBus.$off('global-key-pressed', this.handleKeyPressed);
  },
  methods: {
    handleButtonClick(event) {
      this.handleKeyValue(event.target.value);
    },

    handleKeyPressed(event) {
      if (/[0-9/*+()=.-]/.test(event.key)) {
        this.handleKeyValue(event.key);
        event.stopPropagation();
        return;
      }

      switch (event.key.toLowerCase()) {
        case 'enter':
          this.handleKeyValue('=');
          event.stopPropagation();
          break;
        default:
          break;
      }
    },

    handleKeyValue(value) {
      if (value == null) return;

      if (value === '=') {
        this.equation = removeTrailingOperator(this.equation);
        while (this.openGroups) {
          this.equation += ')';
          this.openGroups -= 1;
        }
        this.equation = SimpleMath.parse(this.equation).total.toString();
        return;
      }

      if (isOperator(value)) {
        this.equation = removeTrailingOperator(this.equation);
        if (this.equation.length === 0) {
          this.equation = '0';
        }
      }

      if (value === '.' && !isDigit(this.equation.slice(-1))) {
        this.equation += '0';
      }

      // avoid numbers with more than one decimals
      if (value === '.' && /[0-9]*\.[0-9]*/.test(this.equation)) {
        return;
      }

      if (value === ')') {
        if (this.openGroups === 0) {
          return;
        }
        this.openGroups -= 1;
      }

      if (value === '(') {
        this.openGroups += 1;
      }

      this.equation += value;
    },
  },
  components: {
    CalculatorButton,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.equation h1 {
  height: 40px;
  background-color: rgba(0,0,0,0.05);
}
</style>



