<template>
  <button class="calc-btn" v-bind:class="[classObject]" v-bind:value="value">{{ value }}</button>
</template>

<script>
export default {
  name: 'CalculatorButton',
  props: {
    value: {
      default() {
        return ' ';
      },
    },
    name: {
      default() {
        return this.value === ' ' ? 'placeholder' : this.value;
      },
    },
    disabled: {
      type: Boolean,
      default() {
        return false;
      },
    },
  },
  computed: {
    classObject() {
      const classes = [`calc-btn-${this.name || this.value}`];
      if (this.disabled) classes.push('disabled');
      return classes;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.calc-btn {
  font-size: 18px;
  font-weight: bold;
  width: 36px;
  height: 36px;
  border: 0;
  margin: 1px;
  padding: 0;
  vertical-align: bottom;
  transition: background-color 150ms;
  background-color: rgb(220, 220, 220);
}

.calc-btn:active {
  background-color: rgb(120, 120, 120);
}

.calc-btn.disabled, .calc-btn.disabled:active {
  color: transparent;
  transition: none;
  background-color: rgb(240, 240, 240);;
}
</style>



