import {
  AdditionOperation,
  SubstractionOperation,
  MultiplicationOperation,
  DivisionOperation,
  OperationGroup,
  ConstantValue,
} from './Operations';
import { isNumber, isDigit, isOperator } from './tools';

function getOperationInstance(operator, leftOperand, rightOperand) {
  if (operator === null) {
    // handles equations that are contant values, e.g. 20.2 or -196
    return new ConstantValue(leftOperand || rightOperand || 0);
  }

  switch (operator) {
    case '+':
      return new AdditionOperation(leftOperand, rightOperand);
    case '-':
      return new SubstractionOperation(leftOperand, rightOperand);
    case '*':
      return new MultiplicationOperation(leftOperand, rightOperand);
    case '/':
      return new DivisionOperation(leftOperand, rightOperand);
    default:
      throw new Error(`unknown operator "${operator}" used with operands ${leftOperand} and ${rightOperand}`);
  }
}

function ensureOperationsPriority(operation) {
  if (operation.leftOperand && operation.leftOperand.priority < operation.priority) {
    const prioritizedOperation = getOperationInstance(
      operation.operator,
      operation.leftOperand.rightOperand,
      operation.rightOperand,
    );

    const deprioritizedOperation = getOperationInstance(
      operation.leftOperand.operator,
      operation.leftOperand.leftOperand,
      prioritizedOperation,
    );

    return deprioritizedOperation;
  }
  return operation;
}

function createOperation(operator, leftOperand, rightOperand) {
  const operation = getOperationInstance(operator, leftOperand, rightOperand);
  return ensureOperationsPriority(operation);
}

class SimpleMath {
  static parse(equation) {
    let pos = 0;
    let currentToken = '';
    let nextToken = '';
    let currentOperator = null;
    let leftOperand = null;
    let lastOperation = null;

    while (pos < equation.length) {
      nextToken = equation[pos];

      if ((currentToken === '' || isNumber(currentToken)) && isDigit(nextToken)) {
        // token is a number
        currentToken += nextToken;
      } else if (currentOperator === null && isOperator(nextToken)) {
        // token is an operator
        leftOperand = currentToken;
        currentToken = '';
        currentOperator = nextToken;
      } else if (currentOperator !== null && isOperator(nextToken)) {
        lastOperation = createOperation(currentOperator, leftOperand, currentToken);
        leftOperand = lastOperation;
        currentToken = '';
        currentOperator = nextToken;
      } else if (nextToken === '(') {
        // find the matching parenthesis
        let openParentheses = 1;
        let lookupPos = pos + 1;
        while (openParentheses && lookupPos < equation.length) {
          if (equation[lookupPos] === '(') {
            openParentheses += 1;
          }
          if (equation[lookupPos] === ')') {
            openParentheses -= 1;
          }
          if (openParentheses > 0) {
            lookupPos += 1;
          }
        }

        // recursively create another equation for the fragment enclosed in the parantheses
        const subEquation = equation.slice(pos + 1, lookupPos);
        const group = new OperationGroup(SimpleMath.parse(subEquation).rootOperation);
        if (currentOperator === null) {
          lastOperation = group;
          leftOperand = group;
          currentToken = group;
        } else {
          currentToken = '';
          lastOperation = createOperation(currentOperator, leftOperand, group);
          leftOperand = lastOperation;
        }
        pos = lookupPos;
      } else if (nextToken === ')') {
        // ignore
      } else {
        throw new Error(`unhandled token ${nextToken} in equation ${equation} at position ${pos}`);
      }
      pos += 1;
    }

    // finalize the last operation if any (ignore groups to avoid extra wrapping)
    if (currentToken && (typeof currentToken === 'string' || currentToken.leftOperand)) {
      lastOperation = createOperation(currentOperator, leftOperand, currentToken);
    }
    currentToken = '';

    return {
      equation,
      rootOperation: lastOperation,
      total: lastOperation.total(),
    };
  }
}

export default SimpleMath;
export { AdditionOperation, SubstractionOperation, MultiplicationOperation, DivisionOperation };
export { OperationGroup };
export { isNumber, isDigit, isOperator };



