import { isNumber } from './tools';

class ConstantValue {
  constructor(value) {
    this.value = parseFloat(value || 0);
    this.priority = Infinity;
  }

  total() {
    return this.value;
  }
}

function normalize(value) {
  return isNumber(value) ?
    new ConstantValue(value) :
    value;
}

class Operation {
  constructor(leftOperand, rightOperand) {
    this.operator = null;
    this.leftOperand = normalize(leftOperand);
    this.rightOperand = normalize(rightOperand);
    this.priority = 0;
  }

  toString() {
    return this.leftOperand.toString() + this.operator + this.rightOperand.toString();
  }
}

class AdditionOperation extends Operation {
  constructor(leftOperand, rightOperand) {
    super(leftOperand, rightOperand);
    this.operator = '+';
  }

  total() {
    return this.leftOperand.total() + this.rightOperand.total();
  }

  toString() {
    return this.leftOperand.toString() + this.operator + this.rightOperand;
  }
}

class SubstractionOperation extends Operation {
  constructor(leftOperand, rightOperand) {
    super(leftOperand, rightOperand);
    this.operator = '-';
  }

  total() {
    return this.leftOperand.total() - this.rightOperand.total();
  }
}

class MultiplicationOperation extends Operation {
  constructor(leftOperand, rightOperand) {
    super(leftOperand, rightOperand);
    this.operator = '*';
    this.priority = 1;
  }

  total() {
    return this.leftOperand.total() * this.rightOperand.total();
  }
}

class DivisionOperation extends Operation {
  constructor(leftOperand, rightOperand) {
    super(leftOperand, rightOperand);
    this.operator = '/';
    this.priority = 1;
  }

  total() {
    return this.leftOperand.total() / this.rightOperand.total();
  }
}

class OperationGroup {
  constructor(operation) {
    this.operation = operation;
    this.priority = Infinity;
  }

  total() {
    return this.operation.total();
  }
}

export default {};
export { AdditionOperation, SubstractionOperation, MultiplicationOperation, DivisionOperation };
export { OperationGroup, ConstantValue };



